import { Injectable } from '@angular/core';
import { BoardService } from './board.service';
import { Cell } from './models/cell';
import { BehaviorSubject } from 'rxjs';
import { Score } from './models/score';
import { GameStateService } from './game-state.service';

@Injectable({
  providedIn: 'root'
})
export class ScoreService {
  private _score = new Score();
  private _scoreSubject = new BehaviorSubject(this._score);
  public boardObservable = this._scoreSubject.asObservable();

  constructor(
    private boardService: BoardService,
    private gameStateService: GameStateService
  ) {
    this.boardService.boardObservable.subscribe((board) => {
      this.calcScore(board);
    });
    this.gameStateService.wildcardsRemaining.subscribe((numberOfWildcards) => {
      this._score.wildcards = numberOfWildcards;
    })
  }

  private calcScore(board: Cell[]) {
    this._score.colors = this.calcColorScore(board);
    this._score.columns = this.calcColumnScore(board);
    this._score.stars = this.calcStarScore(board);
    this._scoreSubject.next(this._score);
  }

  private calcStarScore(board: Cell[]) {
    return board.filter((cell) => cell.star && !cell.isMarked).length * -1;
  }

  private calcColumnScore(board: Cell[]) {
    const prizes = [5, 3, 3, 3, 2, 2, 2, 1, 2, 2, 2, 3, 3, 3, 5];
    let score = 0;
    for (let i=0; i < 15; i++) {
      if (board.filter((cell, index) => index % 15 === i).every(cell => cell.isMarked)) {
        score += prizes[i];
      }
    }

    return score;
  }

  private calcColorScore(board: Cell[]) {
    const colors: string[] = ["Green", "Yellow",  "Blue", "Pink", "Orange" ];
    let score = 0;
    for (let i=0; i < colors.length; i++) {
      if (board.filter((cell) => cell.color === colors[i]).every(cell => cell.isMarked)) {
        score += 5;
      }
    }

    return score;
  }
}
