import { Injectable } from '@angular/core';
import { Cell } from './models/cell';
import { DiceService } from './dice.service';
import { BehaviorSubject } from 'rxjs';
import { MatSnackBar } from '@angular/material/snack-bar';

const boards = [
  "gggyyyyGbbbOyyyogYgYyoopBbooggBgpgggGpppyyoggbppgoObbggyyoPbpoooopbbooopppppBbPpppyYoPbbbOyybbbbpyyyggGoo"
]

@Injectable({
  providedIn: 'root'
})
export class BoardService {
  difficultyLevel = 0;
  private _candidateCells: Cell[] = [];
  private _board: Cell[] = [];
  private rows = 7;
  private cols = 15;
  private boardSize = this.rows*this.cols;
  private _playIsValid = new BehaviorSubject(false);
  public playIsValid =this._playIsValid.asObservable();

  private _boardSubject = new BehaviorSubject(this._board);
  public boardObservable = this._boardSubject.asObservable();

  constructor(private diceService: DiceService, private _snackbar: MatSnackBar) {
    this.resetBoard();
  }

  get board() {
    return this._board;
  }

  get candidates() {
    return this._candidateCells;
  }

  public resetBoard(difficultyLevel: number = 0) {
    this._candidateCells = [];
    this._board = this.stringToBoard(boards[difficultyLevel]);
    this._boardSubject.next(this._board);
  }

  public randomizeBoard() {
    this._candidateCells = [];
    const colors: string[] = ["Green", "Yellow",  "Blue", "Pink", "Orange" ];

    for (let i =0; i < this.boardSize; i++ ) {
      let cell = new Cell(colors[Math.floor(Math.random()*5)], false)
      this._board.push(cell);
    }
    this._boardSubject.next(this._board);
  }

  public playIsComplete(): boolean {
    const numberOfCandidates = this.candidates.length;
    // Using a "?" dice
    if (this.diceService.selectedNumber === "?" && numberOfCandidates > 0 && numberOfCandidates < 7) {
      return true;
    }

    // Using a number dice
    let maxCells = parseInt(this.diceService.selectedNumber)
    if (this.candidates.length === maxCells) {
      return true;
    }

    return false;
  }

  public promoteCandidateCells() {
    const indexes = this._candidateCells.map((cell)=> this._board.indexOf(cell));
    this.markCells(indexes);
    this.clearCandidateCells();
    this._playIsValid.next(this.playIsComplete());
    this._boardSubject.next(this._board);
  }

  public clearCandidateCells() {
    this._candidateCells = [];
  }

  public selectCandidateCell(index: number): void {
      const cell = this._board[index];

      if (this.candidatesContain(cell)) {
        if (this.candidates.length === 1 && this.diceService.selectedColor === "Black") {
          this.diceService.selectedColorWildcard = "";
        }
        this.removeFromCandidates(cell);
        this._playIsValid.next(this.playIsComplete());
        return;
      }
      let maxCells = parseInt(this.diceService.selectedNumber)
      if (isNaN(maxCells)) {
        maxCells = 6;
      }
      if (this.candidates.length === maxCells) {
        this._snackbar.open(
          `Can't mark more than ${maxCells} box${maxCells > 1 ? 'es' : '' }`
        );
        this._playIsValid.next(this.playIsComplete());
        return;
      }

      if (!cell.isMarked && this.cellIsMarkable(index)){
        this._candidateCells.push(cell);
        this._playIsValid.next(this.playIsComplete());
        return;
      }
  }

  public markCells(indexes: number[]) {
    indexes.forEach((i) => this._board[i].isMarked = true);
  }

  public cellIsMarked(index: number): boolean {
      return this._board[index].isMarked;
  }

  public setStars(indexes: number[]) {
      indexes.forEach((i) => this._board[i].star = true);
  }

  private candidatesContain(cell: Cell): boolean {
      return (this._candidateCells.indexOf(cell) !== -1);
  }

  private removeFromCandidates(cell: Cell) {
    const index = this._candidateCells.indexOf(cell);
    this._candidateCells.splice(index, 1);
  }

  private cellIsMarkable(cellIndex: number): boolean {
    const cellIsFirstSelection = this.candidates.length === 0;
    // Cells in column H
    if (cellIsFirstSelection && cellIndex % this.cols === 7) {
      return true;
    }

    // Cells with a marked or candidate neighbor
    const neighbors = this.getCellneighbors(cellIndex);
    for (let i = 0; i < neighbors.length; i++){
      const neighbor = neighbors[i];
      if ((neighbor.isMarked && cellIsFirstSelection) || this.candidatesContain(neighbor)){
        return true;
      }
    }

    this._snackbar.open("Can't mark this box");
    return false;
  }

  private getCellneighbors(cellIndex: number): Cell[] {
    let neighbors: Cell[] = [];
    if (cellIndex % this.cols !== 0) {
      neighbors.push(this._board[cellIndex-1]);
    }
    if (cellIndex % this.cols !== this.cols - 1) {
      neighbors.push(this._board[cellIndex+1]);
    }
    if (cellIndex >= this.cols) {
      neighbors.push(this._board[cellIndex-this.cols]);
    }
    if (cellIndex < this.boardSize - 1 - this.cols) {
      neighbors.push(this._board[cellIndex+this.cols]);
    }
    return neighbors;
  }

  public boardToString(board: Cell[] = this._board) {
    let boardString = "";
    board.forEach(cell => {
      if (cell.star) {
        boardString += cell.color[0].toUpperCase();
      } else {
        boardString += cell.color[0].toLowerCase();
      }
    });
    return boardString;
  }

  public clearBoard() {
    for (let i =0; i < this.boardSize; i++ ) {
      this.board[i] = new Cell("", false);
    }
  }

  public stringToBoard(boardString: string): Cell[] {
    let board = [];
    for(let i =0; i < boardString.length; i++) {
      board.push(this.cellFromChar(boardString[i]));
    }
    return board;
  }

  public cellFromChar(boardChar: string): Cell {
    let colors: {[char: string]: string} = {
      "g": "Green",
      "b": "Blue",
      "p": "Pink",
      "y": "Yellow",
      "o": "Orange"
    }
    return new Cell(
      colors[boardChar.toLowerCase()],
      boardChar === boardChar.toUpperCase()
    );
  }

}
