import { Component, OnInit } from '@angular/core';
import { BoardService } from '../board.service';
import { DiceService } from '../dice.service';
import { Cell } from '../models/cell';
import { GameStateService } from '../game-state.service';
import { GameState } from '../models/game-state';
import { MatSnackBar } from '@angular/material/snack-bar';

@Component({
  selector: 'app-board',
  templateUrl: './board.component.html',
  styleUrls: ['./board.component.scss']
})
export class BoardComponent implements OnInit {
  letters  = ["A", "B", "C", "D", "E", "F", "G", "H", "I", "J", "K", "L", "M", "N", "O"];
  fristPrizeCol = [5, 3, 3, 3, 2, 2, 2, 1, 2, 2, 2, 3, 3, 3, 5];
  secondPrizeCol = [3, 2, 2, 2, 1, 1, 1, 0, 1, 1, 1, 2, 2, 2, 3];

  constructor(
    public boardService: BoardService,
    private diceService: DiceService,
    private gameState: GameStateService,
    private _snackbar: MatSnackBar
    ) {
  }

  ngOnInit() {
  }

  private handleBoxClick(index: number) {
    const cell = this.boardService.board[index];

    // Handle issing selection for number or color
    if (this.gameState.state !== GameState.markingCells) {
      this._snackbar.open(`Select a Number and a Color`);
      return;
    }

    // Handle color wildcard
    if (this.diceService.selectedColor === "Black" && this.diceService.selectedColorWildcard === "") {
      this.diceService.selectedColorWildcard = cell.color;
    }

    if (cell.color !== this.diceService.selectedColor && cell.color !== this.diceService.selectedColorWildcard ) {
      const requiredColor = this.diceService.selectedColor === "Black" ? this.diceService.selectedColorWildcard : this.diceService.selectedColor;
      this._snackbar.open(
        `Only ${requiredColor} boxes can be marked`
      );
      return;
    }
    this.boardService.selectCandidateCell(index);
    console.log(`Clicked box ${index}`);
  }

  public isCandidateCell(cell: Cell): boolean {
    return this.boardService.candidates.indexOf(cell)!==-1;
  }
}
