import { Injectable } from '@angular/core';
import { GameState } from './models/game-state';
import { BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class GameStateService {
  state: GameState = GameState.start;
  private _turn = 0;
  private _wildcards = 8;
  private _wildcardsRemaining = new BehaviorSubject(this._wildcards);
  private _currentTurn = new BehaviorSubject(this._turn);

  public wildcardsRemaining = this._wildcardsRemaining.asObservable();
  public currentTurn = this._currentTurn.asObservable();

  constructor() { }

  get turn() {
    return this._turn;
  }

  public selectedColor() {
    switch (this.state) {
      case GameState.selectedNumber:
        this.state = GameState.markingCells;
        break;

      case GameState.start:
        this.state = GameState.selectedColor;
        break;

      default:
        break;
    }
  }

  public selectedNumber(){
    switch (this.state) {
      case GameState.selectedColor:
        this.state = GameState.markingCells;
        break;

      case GameState.start:
        this.state = GameState.selectedNumber;
        break;

      default:
        break;
    }
  }

  public nextTurn() {
    this.state = GameState.start;
    this._turn++;
    this._currentTurn.next(this._turn);
  }

  public useWildcards(wildcardsUsed: number) {
    this._wildcards -= wildcardsUsed;
    this._wildcardsRemaining.next(this._wildcards);
  }


}
