import { Component, OnInit } from '@angular/core';
import { ScoreService } from '../score.service';

@Component({
  selector: 'app-score',
  templateUrl: './score.component.html',
  styleUrls: ['./score.component.scss']
})
export class ScoreComponent implements OnInit {
  totalScore = 0;
  colorScore = 0;
  columnScore =  0;
  wildcardScore = 0;
  starScore = 0;

  constructor(private scoreService: ScoreService) { }

  ngOnInit() {
    this.scoreService.boardObservable.subscribe((score) => {
      this.colorScore = score.colors;
      this.columnScore = score.columns;
      this.wildcardScore = score.wildcards;
      this.starScore = score.stars;
      this.totalScore = this.colorScore + this.columnScore + this.wildcardScore + this.starScore;
    })
  }

}
