import { Component, OnInit } from '@angular/core';
import { GameStateService } from '../game-state.service';

@Component({
  selector: 'app-wildcard-panel',
  templateUrl: './wildcard-panel.component.html',
  styleUrls: ['./wildcard-panel.component.scss']
})
export class WildcardPanelComponent implements OnInit {
  wildcardsRemaining: number = 0;

  constructor(private gameState: GameStateService) { }

  ngOnInit() {
    this.gameState.wildcardsRemaining.subscribe((numberOfWildcards) => {
      this.wildcardsRemaining = numberOfWildcards;
    })
  }

}
