import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class DiceService {
  numberDice = ["", "", ""];
  colorDice = ["", "", "" ];

  selectedNumber = "";
  selectedColor="";
  selectedColorWildcard="";

  constructor() { }

  rollNumberDice(): string {
    const numbers: string[] = ['1', '2', '3', '4', '5', '?'];
    return numbers[Math.floor(Math.random() * numbers.length)];
  }

  rollColorDice(): string {
    const colors: string[] = ["Green", "Yellow",  "Blue", "Pink", "Orange", "Black" ];
    return colors[Math.floor(Math.random() * colors.length)];
  }

  rollAllDice(){
    this.numberDice.forEach(
      (_x, i)=> this.numberDice[i] = this.rollNumberDice()
    );
    this.colorDice.forEach(
      (_x, i)=> this.colorDice[i] = this.rollColorDice()
    );
  }

  public clearSelection() {
    this.selectedNumber = "";
    this.selectedColor="";
    this.selectedColorWildcard="";
  }
}
