import { Component, OnInit } from '@angular/core';
import { BoardService } from '../board.service';
import { MatSnackBar } from '@angular/material/snack-bar';

interface Clipboard {
  writeText(newClipText: string): Promise<void>;
  // Add any other methods you need here.
}
interface NavigatorClipboard {
  // Only available in a secure context.
  readonly clipboard: Clipboard;
}
interface NavigatorExtended extends NavigatorClipboard {}

@Component({
  selector: 'app-board-editor',
  templateUrl: './board-editor.component.html',
  styleUrls: ['./board-editor.component.scss']
})
export class BoardEditorComponent implements OnInit {
  letters  = ["A", "B", "C", "D", "E", "F", "G", "H", "I", "J", "K", "L", "M", "N", "O"];
  fristPrizeCol = [5, 3, 3, 3, 2, 2, 2, 1, 2, 2, 2, 3, 3, 3, 5];
  colors: {[char: string]: string} = {
    "g": "Green",
    "b": "Blue",
    "p": "Pink",
    "y": "Yellow",
    "o": "Orange"
  }
  tool = "";

  constructor(
    public boardService: BoardService,
    private _snackbar: MatSnackBar
  ) {
    this.boardService.clearBoard();
  }

  ngOnInit() {
  }

  private handleBoxClick(index: number) {
    if (this.tool === "") {
      this._snackbar.open("Select a tool before clicking on boxes");
      return;
    }

    if(this.tool === "S") {
      this.boardService.board[index].star = !this.boardService.board[index].star;
      return;
    }

    this.boardService.board[index].color = this.colors[this.tool.toLowerCase()];
  }

  public onToolChange(initial: string) {
    this.tool = initial;
  }

  public copyBoardString() {
    (navigator as unknown as NavigatorExtended).clipboard.writeText(this.boardService.boardToString());
  }
}

